  
.wrapper {
 max-width: 1200px;
  display: grid;
  grid-template-columns: 250px 1fr;
  margin: 0 150px ;
  grid-template-rows: 50px 2fr;
  margin: 0 auto;
  grid-template-areas: 'n h' 'n c';
}
body {
  background: #373e57;
}
.content {
  margin: 20px;
 
  grid-area: c;
  display: flex;}